	module.exports = {"data":{"markdownRemark":{"html":"<h2>User Acceptance Tests</h2>\n<p>One of the final steps in software testing is user acceptance testing (UAT). UAT makes sure actual users can use the software. Also referred to as beta, application, and end-user testing. </p>\n<p>UAT checks that everything works properly and that there are no crashes. Those of the intended audience should complete the testing; this could be comprised of many people involved from the process and anyone who is able to test before going live.  The feedback from this testing is forwarded to the development team for any specific changes.</p>\n<h4>Why do we need UAT</h4>\n<ul>\n<li>\n<p>Requirement changes might not have communicated to the developers</p>\n</li>\n<li>\n<p>The software may not be delivering actually what it meant for</p>\n</li>\n<li>\n<p>Some logic or business process may need user’s attention</p>\n</li>\n</ul>\n<h4>What is required before we start UAT</h4>\n<ul>\n<li>\n<p>Complete requriement is signed off and is available as documented</p>\n</li>\n<li>\n<p>The code is in working or in demoable condition</p>\n</li>\n<li>\n<p>UAT is environment is ready for access</p>\n</li>\n<li>\n<p>There should not be any defect which will break the code</p>\n</li>\n<li>\n<p>Test data prepared in accordance with the LIVE scneario</p>\n</li>\n</ul>\n<h4>Framework &#x26; tools used</h4>\n<ul>\n<li>FitNesse</li>\n</ul>\n<h4>Articles about UAT</h4>\n<ul>\n<li>\n<p><a href=\"http://blog.debugme.eu/successful-user-acceptance-testing/\">7 KEYS TO A SUCCESSFUL USER ACCEPTANCE TESTING</a></p>\n</li>\n<li>\n<p><a href=\"http://research.ijcaonline.org/volume120/number10/pxc3903533.pdf\">AgileUAT: A Framework for User Acceptance Testing\nbased on User Stories and Acceptance Criteria</a></p>\n</li>\n</ul>","fields":{"slug":"/agile/user-acceptance-tests/"},"frontmatter":{"title":"User Acceptance Tests"}}},"pathContext":{"slug":"/agile/user-acceptance-tests/"}}

